'use client';

export interface AuditData {
  id: string;
  title: string;
  client: string;
  location: string;
  date: Date;
  latitude?: number;
  longitude?: number;
  photos: string[];
  videos: string[];
  audioRecordings: string[];
  transcriptions: string[];
  anomalies: AnomalyData[];
  floorPlan?: string;
  signature?: string;
  status: 'draft' | 'completed' | 'synced';
  createdAt: Date;
  updatedAt: Date;
}

export interface AnomalyData {
  id: string;
  type: string;
  description: string;
  severity: 'low' | 'medium' | 'high' | 'critical';
  photo?: string;
  location: { x: number; y: number };
  suggestions: string[];
  createdAt: Date;
}

export interface ChatMessage {
  id: string;
  auditId: string;
  userId: string;
  userName: string;
  message: string;
  type: 'text' | 'audio';
  audioData?: string;
  timestamp: Date;
  synced: boolean;
}

class IndexedDBManager {
  private dbName = 'AuditProDB';
  private version = 1;
  private db: IDBDatabase | null = null;

  async init(): Promise<void> {
    return new Promise((resolve, reject) => {
      const request = indexedDB.open(this.dbName, this.version);

      request.onerror = () => reject(request.error);
      request.onsuccess = () => {
        this.db = request.result;
        resolve();
      };

      request.onupgradeneeded = (event) => {
        const db = (event.target as IDBOpenDBRequest).result;

        // Audits store
        if (!db.objectStoreNames.contains('audits')) {
          const auditStore = db.createObjectStore('audits', { keyPath: 'id' });
          auditStore.createIndex('client', 'client', { unique: false });
          auditStore.createIndex('date', 'date', { unique: false });
          auditStore.createIndex('status', 'status', { unique: false });
        }

        // Chat messages store
        if (!db.objectStoreNames.contains('messages')) {
          const messageStore = db.createObjectStore('messages', { keyPath: 'id' });
          messageStore.createIndex('auditId', 'auditId', { unique: false });
          messageStore.createIndex('timestamp', 'timestamp', { unique: false });
        }

        // Media files store
        if (!db.objectStoreNames.contains('media')) {
          db.createObjectStore('media', { keyPath: 'id' });
        }
      };
    });
  }

  async saveAudit(audit: AuditData): Promise<void> {
    if (!this.db) await this.init();
    
    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(['audits'], 'readwrite');
      const store = transaction.objectStore('audits');
      const request = store.put(audit);

      request.onerror = () => reject(request.error);
      request.onsuccess = () => resolve();
    });
  }

  async getAudit(id: string): Promise<AuditData | null> {
    if (!this.db) await this.init();

    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(['audits'], 'readonly');
      const store = transaction.objectStore('audits');
      const request = store.get(id);

      request.onerror = () => reject(request.error);
      request.onsuccess = () => resolve(request.result || null);
    });
  }

  async getAllAudits(): Promise<AuditData[]> {
    if (!this.db) await this.init();

    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(['audits'], 'readonly');
      const store = transaction.objectStore('audits');
      const request = store.getAll();

      request.onerror = () => reject(request.error);
      request.onsuccess = () => resolve(request.result || []);
    });
  }

  async deleteAudit(id: string): Promise<void> {
    if (!this.db) await this.init();

    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(['audits'], 'readwrite');
      const store = transaction.objectStore('audits');
      const request = store.delete(id);

      request.onerror = () => reject(request.error);
      request.onsuccess = () => resolve();
    });
  }

  async saveMessage(message: ChatMessage): Promise<void> {
    if (!this.db) await this.init();

    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(['messages'], 'readwrite');
      const store = transaction.objectStore('messages');
      const request = store.put(message);

      request.onerror = () => reject(request.error);
      request.onsuccess = () => resolve();
    });
  }

  async getMessages(auditId: string): Promise<ChatMessage[]> {
    if (!this.db) await this.init();

    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(['messages'], 'readonly');
      const store = transaction.objectStore('messages');
      const index = store.index('auditId');
      const request = index.getAll(auditId);

      request.onerror = () => reject(request.error);
      request.onsuccess = () => resolve(request.result || []);
    });
  }

  async saveMedia(id: string, data: string): Promise<void> {
    if (!this.db) await this.init();

    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(['media'], 'readwrite');
      const store = transaction.objectStore('media');
      const request = store.put({ id, data });

      request.onerror = () => reject(request.error);
      request.onsuccess = () => resolve();
    });
  }

  async getMedia(id: string): Promise<string | null> {
    if (!this.db) await this.init();

    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction(['media'], 'readonly');
      const store = transaction.objectStore('media');
      const request = store.get(id);

      request.onerror = () => reject(request.error);
      request.onsuccess = () => resolve(request.result?.data || null);
    });
  }
}

export const dbManager = new IndexedDBManager();
