'use client';

import { useState, useRef, useCallback, useEffect } from 'react';
import { Button } from '@/components/ui/button';

interface AudioRecorderProps {
  onRecordingComplete: (data: string) => void;
}

export function AudioRecorder({ onRecordingComplete }: AudioRecorderProps) {
  const [isRecording, setIsRecording] = useState(false);
  const [isPaused, setIsPaused] = useState(false);
  const [recordingTime, setRecordingTime] = useState(0);
  const [error, setError] = useState<string | null>(null);
  const [audioLevel, setAudioLevel] = useState(0);

  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const streamRef = useRef<MediaStream | null>(null);
  const chunksRef = useRef<Blob[]>([]);
  const timerRef = useRef<NodeJS.Timeout | null>(null);
  const audioContextRef = useRef<AudioContext | null>(null);
  const analyserRef = useRef<AnalyserNode | null>(null);
  const animationRef = useRef<number | null>(null);

  const updateAudioLevel = useCallback(() => {
    if (!analyserRef.current) return;

    const dataArray = new Uint8Array(analyserRef.current.frequencyBinCount);
    analyserRef.current.getByteFrequencyData(dataArray);
    
    const average = dataArray.reduce((sum, value) => sum + value, 0) / dataArray.length;
    setAudioLevel(average / 255);

    if (isRecording && !isPaused) {
      animationRef.current = requestAnimationFrame(updateAudioLevel);
    }
  }, [isRecording, isPaused]);

  const startRecording = useCallback(async () => {
    try {
      setError(null);
      const stream = await navigator.mediaDevices.getUserMedia({ 
        audio: {
          echoCancellation: true,
          noiseSuppression: true,
          sampleRate: 44100
        }
      });
      
      streamRef.current = stream;
      chunksRef.current = [];

      // Setup audio visualization
      audioContextRef.current = new AudioContext();
      const source = audioContextRef.current.createMediaStreamSource(stream);
      analyserRef.current = audioContextRef.current.createAnalyser();
      analyserRef.current.fftSize = 256;
      source.connect(analyserRef.current);

      const mediaRecorder = new MediaRecorder(stream, {
        mimeType: 'audio/webm;codecs=opus'
      });

      mediaRecorder.ondataavailable = (event) => {
        if (event.data.size > 0) {
          chunksRef.current.push(event.data);
        }
      };

      mediaRecorder.onstop = () => {
        const blob = new Blob(chunksRef.current, { type: 'audio/webm' });
        const reader = new FileReader();
        reader.onloadend = () => {
          onRecordingComplete(reader.result as string);
        };
        reader.readAsDataURL(blob);
        cleanup();
      };

      mediaRecorderRef.current = mediaRecorder;
      mediaRecorder.start();
      setIsRecording(true);
      setRecordingTime(0);

      // Start timer
      timerRef.current = setInterval(() => {
        setRecordingTime(prev => prev + 1);
      }, 1000);

      // Start audio level monitoring
      updateAudioLevel();
    } catch (err) {
      setError('Impossible d\'accéder au microphone. Vérifiez les permissions.');
      console.error('Microphone access error:', err);
    }
  }, [onRecordingComplete, updateAudioLevel]);

  const pauseRecording = useCallback(() => {
    if (mediaRecorderRef.current && isRecording && !isPaused) {
      mediaRecorderRef.current.pause();
      setIsPaused(true);
      if (timerRef.current) {
        clearInterval(timerRef.current);
      }
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current);
      }
    }
  }, [isRecording, isPaused]);

  const resumeRecording = useCallback(() => {
    if (mediaRecorderRef.current && isRecording && isPaused) {
      mediaRecorderRef.current.resume();
      setIsPaused(false);
      
      // Resume timer
      timerRef.current = setInterval(() => {
        setRecordingTime(prev => prev + 1);
      }, 1000);

      // Resume audio level monitoring
      updateAudioLevel();
    }
  }, [isRecording, isPaused, updateAudioLevel]);

  const stopRecording = useCallback(() => {
    if (mediaRecorderRef.current && isRecording) {
      mediaRecorderRef.current.stop();
      setIsRecording(false);
      setIsPaused(false);
    }
  }, [isRecording]);

  const cleanup = useCallback(() => {
    if (streamRef.current) {
      streamRef.current.getTracks().forEach(track => track.stop());
      streamRef.current = null;
    }
    if (timerRef.current) {
      clearInterval(timerRef.current);
      timerRef.current = null;
    }
    if (animationRef.current) {
      cancelAnimationFrame(animationRef.current);
      animationRef.current = null;
    }
    if (audioContextRef.current) {
      audioContextRef.current.close();
      audioContextRef.current = null;
    }
    setRecordingTime(0);
    setAudioLevel(0);
  }, []);

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  useEffect(() => {
    return cleanup;
  }, [cleanup]);

  return (
    <div className="space-y-6">
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4">
          <p className="text-red-800">{error}</p>
        </div>
      )}

      {!isRecording ? (
        <div className="text-center py-8">
          <div className="text-6xl mb-4">🎤</div>
          <Button onClick={startRecording} size="lg">
            <span className="mr-2">🔴</span>
            Commencer l'enregistrement
          </Button>
        </div>
      ) : (
        <div className="space-y-6">
          {/* Recording Status */}
          <div className="text-center">
            <div className="flex items-center justify-center gap-2 mb-2">
              <div className={`w-3 h-3 rounded-full ${
                isPaused ? 'bg-yellow-500' : 'bg-red-500 animate-pulse'
              }`}></div>
              <span className="text-lg font-medium">
                {isPaused ? 'EN PAUSE' : 'ENREGISTREMENT'}
              </span>
            </div>
            <div className="text-2xl font-mono">
              {formatTime(recordingTime)}
            </div>
          </div>

          {/* Audio Level Visualization */}
          <div className="flex justify-center">
            <div className="flex items-end gap-1 h-16">
              {Array.from({ length: 20 }, (_, i) => (
                <div
                  key={i}
                  className={`w-2 bg-gradient-to-t from-green-500 to-red-500 rounded-t transition-all duration-100 ${
                    audioLevel * 20 > i ? 'opacity-100' : 'opacity-20'
                  }`}
                  style={{
                    height: `${Math.max(4, (audioLevel * 20 > i ? (i + 1) * 3 : 1))}px`
                  }}
                />
              ))}
            </div>
          </div>

          {/* Controls */}
          <div className="flex justify-center gap-4">
            {!isPaused ? (
              <Button onClick={pauseRecording} variant="outline" size="lg">
                <span className="mr-2">⏸️</span>
                Pause
              </Button>
            ) : (
              <Button onClick={resumeRecording} variant="outline" size="lg">
                <span className="mr-2">▶️</span>
                Reprendre
              </Button>
            )}
            
            <Button onClick={stopRecording} size="lg" variant="destructive">
              <span className="mr-2">⏹️</span>
              Arrêter
            </Button>
          </div>

          {/* Recording Tips */}
          <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
            <h4 className="font-medium text-blue-900 mb-2">💡 Conseils d'enregistrement</h4>
            <ul className="text-sm text-blue-800 space-y-1">
              <li>• Parlez clairement et à un rythme normal</li>
              <li>• Maintenez une distance constante du microphone</li>
              <li>• Évitez les bruits de fond</li>
              <li>• La transcription sera automatique après l'arrêt</li>
            </ul>
          </div>
        </div>
      )}
    </div>
  );
}
