'use client';

import { useState, useRef, useEffect } from 'react';
import { useTranslation } from '@/components/Providers';
import { useGeolocation } from '@/hooks/useGeolocation';
import { useSync } from '@/hooks/useSync';
import { dbManager, AuditData } from '@/lib/indexedDB';
import { transcribeAudio } from '@/lib/ai';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { CameraCapture } from '@/components/CameraCapture';
import { AudioRecorder } from '@/components/AudioRecorder';
import { v4 as uuidv4 } from 'uuid';

export default function CapturePage() {
  const { t } = useTranslation();
  const { latitude, longitude, error: locationError } = useGeolocation();
  const { addPendingItem } = useSync();
  
  const [currentAudit, setCurrentAudit] = useState<Partial<AuditData>>({
    id: uuidv4(),
    title: '',
    client: '',
    location: '',
    date: new Date(),
    photos: [],
    videos: [],
    audioRecordings: [],
    transcriptions: [],
    anomalies: [],
    status: 'draft',
    createdAt: new Date(),
    updatedAt: new Date(),
  });

  const [activeTab, setActiveTab] = useState<'photo' | 'video' | 'audio'>('photo');
  const [comment, setComment] = useState('');
  const [isTranscribing, setIsTranscribing] = useState(false);
  const [saving, setSaving] = useState(false);

  const handleMediaCapture = async (mediaData: string, type: 'photo' | 'video' | 'audio') => {
    const mediaId = uuidv4();
    
    // Save media to IndexedDB
    await dbManager.saveMedia(mediaId, mediaData);
    
    // Update current audit
    setCurrentAudit(prev => ({
      ...prev,
      [type === 'photo' ? 'photos' : type === 'video' ? 'videos' : 'audioRecordings']: [
        ...(prev[type === 'photo' ? 'photos' : type === 'video' ? 'videos' : 'audioRecordings'] || []),
        mediaId
      ],
      updatedAt: new Date(),
    }));

    // If audio, transcribe it
    if (type === 'audio') {
      setIsTranscribing(true);
      try {
        // Convert base64 to blob for transcription
        const response = await fetch(mediaData);
        const blob = await response.blob();
        const transcription = await transcribeAudio(blob);
        
        setCurrentAudit(prev => ({
          ...prev,
          transcriptions: [...(prev.transcriptions || []), transcription.text],
          updatedAt: new Date(),
        }));
      } catch (error) {
        console.error('Transcription failed:', error);
      } finally {
        setIsTranscribing(false);
      }
    }
  };

  const handleSaveAudit = async () => {
    if (!currentAudit.title || !currentAudit.client) {
      alert('Veuillez remplir le titre et le client');
      return;
    }

    setSaving(true);
    try {
      const auditToSave: AuditData = {
        ...currentAudit,
        latitude,
        longitude,
        updatedAt: new Date(),
      } as AuditData;

      await dbManager.saveAudit(auditToSave);
      addPendingItem();
      
      // Reset form
      setCurrentAudit({
        id: uuidv4(),
        title: '',
        client: '',
        location: '',
        date: new Date(),
        photos: [],
        videos: [],
        audioRecordings: [],
        transcriptions: [],
        anomalies: [],
        status: 'draft',
        createdAt: new Date(),
        updatedAt: new Date(),
      });
      setComment('');
      
      alert('Audit sauvegardé avec succès!');
    } catch (error) {
      console.error('Error saving audit:', error);
      alert('Erreur lors de la sauvegarde');
    } finally {
      setSaving(false);
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-3xl font-bold text-foreground">{t('capture.title')}</h1>
        <p className="text-muted-foreground mt-2">
          Capturez photos, vidéos et commentaires audio pour votre audit
        </p>
      </div>

      {/* Location Status */}
      <Card>
        <CardContent className="pt-6">
          <div className="flex items-center gap-2">
            <span className="text-lg">📍</span>
            <div>
              {locationError ? (
                <span className="text-red-600">{locationError}</span>
              ) : latitude && longitude ? (
                <span className="text-green-600">
                  Position: {latitude.toFixed(6)}, {longitude.toFixed(6)}
                </span>
              ) : (
                <span className="text-yellow-600">Localisation en cours...</span>
              )}
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Audit Info Form */}
      <Card>
        <CardHeader>
          <CardTitle>Informations de l'audit</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium mb-2">Titre de l'audit</label>
              <Input
                value={currentAudit.title || ''}
                onChange={(e) => setCurrentAudit(prev => ({ ...prev, title: e.target.value }))}
                placeholder="Ex: Audit sécurité incendie"
              />
            </div>
            <div>
              <label className="block text-sm font-medium mb-2">Client</label>
              <Input
                value={currentAudit.client || ''}
                onChange={(e) => setCurrentAudit(prev => ({ ...prev, client: e.target.value }))}
                placeholder="Nom du client"
              />
            </div>
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Lieu</label>
            <Input
              value={currentAudit.location || ''}
              onChange={(e) => setCurrentAudit(prev => ({ ...prev, location: e.target.value }))}
              placeholder="Adresse ou description du lieu"
            />
          </div>
        </CardContent>
      </Card>

      {/* Media Capture Tabs */}
      <Card>
        <CardHeader>
          <div className="flex space-x-1 bg-muted p-1 rounded-lg">
            {(['photo', 'video', 'audio'] as const).map((tab) => (
              <button
                key={tab}
                onClick={() => setActiveTab(tab)}
                className={`flex-1 py-2 px-4 rounded-md text-sm font-medium transition-colors ${
                  activeTab === tab
                    ? 'bg-background text-foreground shadow-sm'
                    : 'text-muted-foreground hover:text-foreground'
                }`}
              >
                {tab === 'photo' && '📷'} {tab === 'video' && '🎥'} {tab === 'audio' && '🎤'}
                {' '}
                {t(`capture.${tab}`)}
              </button>
            ))}
          </div>
        </CardHeader>
        <CardContent>
          {activeTab === 'photo' && (
            <CameraCapture
              onCapture={(data) => handleMediaCapture(data, 'photo')}
              type="photo"
            />
          )}
          {activeTab === 'video' && (
            <CameraCapture
              onCapture={(data) => handleMediaCapture(data, 'video')}
              type="video"
            />
          )}
          {activeTab === 'audio' && (
            <AudioRecorder
              onRecordingComplete={(data) => handleMediaCapture(data, 'audio')}
            />
          )}
        </CardContent>
      </Card>

      {/* Comment Section */}
      <Card>
        <CardHeader>
          <CardTitle>{t('capture.comment')}</CardTitle>
        </CardHeader>
        <CardContent>
          <Textarea
            value={comment}
            onChange={(e) => setComment(e.target.value)}
            placeholder="Ajoutez vos commentaires sur cet audit..."
            rows={4}
          />
        </CardContent>
      </Card>

      {/* Transcription Status */}
      {isTranscribing && (
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-2">
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-primary"></div>
              <span>{t('capture.transcribing')}</span>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Transcriptions Display */}
      {currentAudit.transcriptions && currentAudit.transcriptions.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>Transcriptions</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              {currentAudit.transcriptions.map((transcription, index) => (
                <div key={index} className="p-3 bg-muted/50 rounded-lg">
                  <p className="text-sm">{transcription}</p>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}

      {/* Captured Media Summary */}
      {(currentAudit.photos?.length || currentAudit.videos?.length || currentAudit.audioRecordings?.length) && (
        <Card>
          <CardHeader>
            <CardTitle>Médias capturés</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-3 gap-4 text-center">
              <div>
                <div className="text-2xl">📷</div>
                <div className="text-sm text-muted-foreground">
                  {currentAudit.photos?.length || 0} photos
                </div>
              </div>
              <div>
                <div className="text-2xl">🎥</div>
                <div className="text-sm text-muted-foreground">
                  {currentAudit.videos?.length || 0} vidéos
                </div>
              </div>
              <div>
                <div className="text-2xl">🎤</div>
                <div className="text-sm text-muted-foreground">
                  {currentAudit.audioRecordings?.length || 0} audios
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Save Button */}
      <div className="flex justify-end">
        <Button 
          onClick={handleSaveAudit} 
          disabled={saving || !currentAudit.title || !currentAudit.client}
          size="lg"
        >
          {saving ? (
            <>
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-current mr-2"></div>
              Sauvegarde...
            </>
          ) : (
            <>
              <span className="mr-2">💾</span>
              {t('capture.save')}
            </>
          )}
        </Button>
      </div>
    </div>
  );
}
